.slick-initialized .slick-slide {
    display: flex;
}
.slick-prev,
.slick-next {
    position: absolute;
    top: -51px;
    right: 15px;

    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;

    background: #FFFFFF;
    border: 1px solid #D6D6D6;
    border-radius: 4px;
    transition: transform 0.4s;
    transition: border-color 0.4s;
    &:hover {
        border-color: #7c7c7c
    }
    &:active {
        transform: scale(0.95);
    }
}
.slick-prev {
    right: 65px;
    img {
        transform: rotate(180deg);
    }
}

.slick-disabled {
    opacity: 0.5;
}