// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: 'PT Root UI Web', sans-serif;
  --content-width: 1743px;
  --container-offset: 0px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --main-color: #ed4f4f;
  /* #3a82d0 */
  /* #d81123 */
}
